





















import Vue from "vue";
import Component from "vue-class-component";
import { CommentModel, RequestData } from "@/service/interfaces";
import { AppService } from "@/service/app.service";
import { ApiService } from "@/service/api.service";
import CommentPreviewComponent from "@/components/forum/CommentPreview.vue";

@Component({
  components: {
    CommentPreviewComponent,
  },
})
export default class PostList extends Vue {
  comments: Array<CommentModel> = [];
  app = AppService.instance;
  public api: ApiService = ApiService.instance;

  loadingComments = false;

  options: RequestData = {
    userIdx: "userIdx=",
  };

  mounted(): void {
    this.options.userIdx = this.$route.params.userIdx;
    this.loadComments();
  }

  async loadComments(): Promise<void> {
    console.log(this.options);
    this.loadingComments = true;
    try {
      this.comments = await this.app.api.commentSearch(this.options);
      // console.log(this.comments);
      this.loadingComments = false;
    } catch (error) {
      this.app.error(error);
      this.loadingComments = false;
    }
  }
}
