






















import { CommentModel, PostModel } from "@/service/interfaces";
import Vue from "vue";
import Component from "vue-class-component";
import UserMenu from "@/components/forum/UserMenu.vue";

@Component({
  props: ["parent", "size"],
  components: {
    UserMenu,
  },
})
export default class UserAvatar extends Vue {
  parent!: PostModel & CommentModel;
  size!: number;

  defaultSize = 3;
  src = "";
  id = "user-avatar-popover-";

  mounted(): void {
    if (this.size) this.defaultSize = this.size;
    this.src = this.parent.user.photoUrl;
    this.id += this.parent.idx;
  }
}
